<template>
  <div>
    <el-row :gutter="20">
      <el-col class="mb20" :xl="6" :lg="6" :md="10" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>标签编辑</span>
          </div>
          <el-form :model="editParams" label-position="right" label-width="60px">
            <el-form-item label="名称">
              <el-input v-model="editParams.name" autocomplete="off" placeholder="名称：建议2-4字以内"></el-input>
            </el-form-item>
            <el-form-item label="字段">
              <el-input
                v-model="editParams.field"
                autocomplete="off"
                placeholder="以字母开头，下划线分隔，例：price"
              ></el-input>
            </el-form-item>
            <el-form-item label="类型">
              <el-select v-model="editParams.type" placeholder="请选类型" @change="typeChange">
                <el-option label="单行文本" value="input"></el-option>
                <el-option label="多行文本" value="textarea"></el-option>
                <el-option label="日期选择" value="date"></el-option>
                <el-option label="图片上传" value="image"></el-option>
                <el-option label="附件上传" value="file"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="字段值">
              <el-input
                v-if="editParams.type=='input'"
                v-model="editParams.value"
                :placeholder="editParams.name"
              ></el-input>
              <el-input
                v-else-if="editParams.type=='textarea'"
                type="textarea"
                v-model="editParams.value"
                :placeholder="editParams.name"
              ></el-input>
              <el-date-picker
                v-else-if="editParams.type=='date'"
                v-model="editParams.value"
                type="date"
                value-format="yyyy-MM-dd"
                :placeholder="editParams.name"
              ></el-date-picker>
              <el-upload
                v-else-if="editParams.type=='image'"
                action="/api/admin/upload/image"
                :on-remove="handleRemove"
                :on-success="onSuccess"
                :file-list="fileList"
                multiple
                list-type="picture"
              >
                <el-button size="small" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
              </el-upload>
              <el-upload
                v-else-if="editParams.type=='file'"
                action="/api/admin/upload/file"
                :on-remove="handleRemove"
                :on-success="onSuccess"
                :file-list="fileList"
                multiple
              >
                <el-button size="small" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">不超过500kb</div>
              </el-upload>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="addLabel">保存</el-button>
              <el-button>取消</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
      <el-col class="mb20" :xl="18" :lg="18" :md="14" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>所有标签</span>
          </div>
          <el-table
            :data="labels"
            style="margin-bottom:20px;"
            v-loading="tableLoading"
            element-loading-background="rgba(0, 0, 0, 0.3)"
            element-loading-text="数据正在加载中"
            element-loading-spinner="el-icon-loading"
          >
            <el-table-column prop="id" label="ID" width="60"></el-table-column>
            <el-table-column prop="name" label="名称"></el-table-column>
            <el-table-column prop="field" label="字段"></el-table-column>
            <el-table-column prop="value" label="值" show-overflow-tooltip></el-table-column>
            <el-table-column prop="type" label="类型"></el-table-column>
            <el-table-column prop="createTime" label="创建时间"></el-table-column>
            <el-table-column label="操作" width="150">
              <template slot-scope="scope">
                <el-button type="text" @click="edit(scope.row)">修改</el-button>
                <el-popconfirm
                  title="确定删除本条数据吗？删除后不可恢复。"
                  style="margin-left:10px;"
                  cancelButtonType="default"
                  @onConfirm="del(scope.row)"
                >
                  <el-button type="text" slot="reference">删除</el-button>
                </el-popconfirm>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import labelApi from "@/api/labelApi";
export default {
  components: {},
  data() {
    return {
      fileList: [],
      editParams: { type: "input" },
      tableLoading: false,
      labels: []
    };
  },
  created() {
    this.getLabels();
  },
  methods: {
    getLabels() {
      this.tableLoading = true;
      labelApi.findAll().then(res => {
        this.labels = res.data;
        this.tableLoading = false;
      });
    },
    typeChange(val) {},
    addLabel() {
      if (!this.editParams.name) {
        this.$message.warning("请填写名称");
        return false;
      }
      if (!this.editParams.field) {
        this.$message.warning("请填写字段");
        return false;
      }
      if (!this.editParams.value) {
        this.$message.warning("请填写字段值");
        return false;
      }
      if (this.editParams.id) {
        labelApi.update(this.editParams).then(res => {
          this.$message.success("保存成功");
          this.getLabels();
        });
      } else {
        labelApi.create(this.editParams).then(res => {
          this.$message.success("保存成功");
          this.getLabels();
        });
      }
    },
    edit(row) {
      this.editParams = row;
      if (row.type == "file" || row.type == "image") {
        let value = row.value;
        value.split(",").map(v => {
          this.fileList.push({ name: v.split("@@")[1], url: v });
        });
      }
    },
    del(row) {
      labelApi.delete(row.id).then(res => {
        this.$message.success("删除成功");
        this.getLabels();
      });
    },
    resetForm() {},
    onSuccess(res, file, fileList) {
      file.url = res.data.url;
      this.editParams.value = fileList.map(f => f.url).join(",");
    },
    handleRemove(file, fileList) {
      this.editParams.value = fileList.map(f => f.url).join(",");
    }
  }
};
</script>

<style lang="scss" scoped>
.vertical-form {
  .line {
    text-align: center;
  }
}
</style>>
