import request from '@/utils/request'

const baseUrl = '/api/admin/label'

const labelApi = {}

labelApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

labelApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

labelApi.get = labelId => {
  return request({
    url: `${baseUrl}/${labelId}`,
    method: 'get'
  })
}

labelApi.update = (params) => {
  return request({
    url: `${baseUrl}/update`,
    data: params,
    method: 'put'
  })
}

labelApi.create = label => {
  return request({
    url: `${baseUrl}/save`,
    data: label,
    method: 'post'
  })
}

labelApi.delete = labelId => {
  return request({
    url: `${baseUrl}/${labelId}`,
    method: 'delete'
  })
}

export default labelApi
